
.goods{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	position: absolute;
	top:174px;
	width: 100%;
	bottom: 46px;
	overflow: hidden;
}
.goods .menu-wrapper{
	-webkit-box-flex: 0;
	    -ms-flex: 0 0 80px;
	        flex: 0 0 80px;
	width: 80px;
	background: #f3f5f7;
}
.goods .foods-wrapper{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1
}
.goods .menu-wrapper .menu-item {
	display: table;
	height: 54px;
	width: 56px;
	padding: 0 12px;
	line-height: 14px;
}
.goods .menu-wrapper .menu-item .text {
	display: table-cell;
	width: 56px;
	vertical-align: middle;
	position: relative;
	font-size: 12px;
}
.goods .foods-wrapper .title {
	padding-left: 14px;
	height: 26px;
	line-height: 26px;
	border-left: 2px solid rgb(217, 221, 225);
	font-size: 12px;
	color: rgb(147, 153, 159);
	background: rgb(243, 245, 247);
}
.goods .foods-wrapper .food-item {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	margin: 18px;
	padding-bottom: 18px;
	position: relative;
}
.goods .foods-wrapper .food-item .icon {
	-webkit-box-flex: 0;
	-ms-flex: 0 0 57px;
	flex: 0 0 57px;
	margin-right: 10px;
}
.goods .foods-wrapper .food-item .content {
	-webkit-box-flex: 1;
	-ms-flex: 1;
	flex: 1;
}
.goods .foods-wrapper .food-item .content .name {
	margin: 2px 0 8px 0;
	height: 14px;
	line-height: 14px;
	font-size: 14px;
	color: rgb(7, 17, 27);
}
.goods .foods-wrapper .food-item .content .desc {
	line-height: 12px;
	margin-bottom: 8px;
}
.goods .foods-wrapper .food-item .content .desc, .goods .foods-wrapper .food-item .content .extra {
	line-height: 10px;
	font-size: 10px;
	color: rgb(147, 153, 159);
}
.goods .foods-wrapper .food-item .content .extra .count {
	margin-right: 12px;
}
.goods .foods-wrapper .food-item .content .price {
	font-weight: 700;
	line-height: 24px;
}
.goods .foods-wrapper .food-item .content .price .now {
	margin-right: 8px;
	font-size: 14px;
	color: rgb(240, 20, 20);
}
.goods .foods-wrapper .food-item .content .price .old {
	text-decoration: line-through;
	font-size: 10px;
	color: rgb(147, 153, 159);
}
.goods .foods-wrapper .food-item .content .price .old {
	text-decoration: line-through;
	font-size: 10px;
	color: rgb(147, 153, 159);
}
.goods .foods-wrapper .food-item .content .cartcontrol-wrapper {
	position: absolute;
	right: 0;
	bottom: 12px;
}



