
.ratings .overview {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 18px 0;
}
.ratings .overview .overview-left {
  -webkit-box-flex: 0;
  -ms-flex: 0 0 137px;
  flex: 0 0 137px;
  padding: 6px 0;
  width: 137px;
  border-right: 1px solid rgba(7,17,27,0.1);
  text-align: center;
}
.ratings .overview .overview-left .score {
  margin-bottom: 6px;
  line-height: 28px;
  font-size: 24px;
  color: rgb(255, 153, 0);
}
.ratings .overview .overview-left .title {
  margin-bottom: 8px;
  line-height: 12px;
  font-size: 12px;
  color: rgb(7, 17, 27);
}
.ratings .overview .overview-left .rank {
  line-height: 10px;
  font-size: 10px;
  color: rgb(147, 153, 159);
}
.ratings .overview .overview-right {
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  padding: 6px 0 6px 24px;
}
.ratings .overview .overview-right .score-wrapper {
  margin-bottom: 8px;
  font-size: 0;
}
.ratings .overview .overview-right .score-wrapper .title {
  display: inline-block;
  line-height: 18px;
  vertical-align: top;
  font-size: 12px;
  color: rgb(7, 17, 27);
}
.ratings .overview .overview-right .score-wrapper .star {
  display: inline-block;
  margin: 0 7px;
  vertical-align: top;
}
.ratings .overview .overview-right .score-wrapper .score {
  display: inline-block;
  line-height: 18px;
  vertical-align: top;
  font-size: 12px;
  color: rgb(255, 153, 0);
  padding-left:0;
}
.ratings .overview .overview-right .delivery-wrapper .delivery {
  margin-left: 12px;
  font-size: 12px;
  color: rgb(147, 153, 159);
}
.ratings .overview .overview-right .delivery-wrapper .title {
  line-height: 18px;
  font-size: 12px;
  color: rgb(7, 17, 27);
}
.split {
  width: 100%;
  height: 16px;
  border-top: 1px solid rgba(7,17,27,0.1);
  border-bottom: 1px solid rgba(7,17,27,0.1);
  background: rgb(243, 245, 247);
}
@media only screen and (max-width: 320px){
.ratings .overview .overview-left {
    -webkit-box-flex: 0;
    -ms-flex: 0 0 120px;
    flex: 0 0 120px;
    width: 120px;
}
.ratings .overview .overview-right {
    padding-left: 5px;
}
}



