/*清除浮动*/
.clearfix{
	display:inline-block;
}
.clearfix:after{
	content:".";
	display:block;
	height:0;
	clear:both;
	visibility:hidden
}
.border-1px{
	border-bottom:1px solid #B7C4E9;
}
body, html {
    line-height: 1;
    font-weight: 200;
    font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif;
}


