
.cartcontrol {
  font-size: 0;
}
.cartcontrol .cart-decrease {
  display: inline-block;
  padding: 6px;
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.cartcontrol .cart-decrease .inner {
  display: inline-block;
  line-height: 24px;
  font-size: 24px;
  color: rgb(0, 160, 220);
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
  -webkit-transform: rotate(0);
  transform: rotate(0);
}
.cartcontrol .cart-count {
  display: inline-block;
  vertical-align: top;
  width: 12px;
  padding-top: 6px;
  line-height: 24px;
  text-align: center;
  font-size: 10px;
  color: rgb(147, 153, 159);
}
.cartcontrol .cart-add {
  display: inline-block;
  padding: 6px;
  line-height: 24px;
  font-size: 24px;
  color: rgb(0, 160, 220);
}



