
.shopcart {
		position: fixed;
		left: 0;
		bottom: 0;
		z-index: 50;
		width: 100%;
		height: 48px;
}
.shopcart .content {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		background: rgb(20, 29, 39);
		font-size: 0;
		color: rgba(255,255,255,0.4);
}
.shopcart .content .content-left {
		-webkit-box-flex: 1;
		-ms-flex: 1;
		flex: 1;
}
.shopcart .content .content-left .logo-wrapper {
		display: inline-block;
		vertical-align: top;
		position: relative;
		top: -10px;
		margin: 0 12px;
		padding: 6px;
		width: 56px;
		height: 56px;
		box-sizing: border-box;
		border-radius: 50%;
		background: rgb(20, 29, 39);
}
.shopcart .content .content-left .logo-wrapper .logo {
		width: 100%;
		height: 100%;
		border-radius: 50%;
		text-align: center;
		background: rgb(43, 52, 60);
}
.shopcart .content .content-left .logo-wrapper .logo .icon-shopping_cart {
		line-height: 44px;
		font-size: 24px;
		color: rgb(128, 133, 138);
}
.shopcart .content .content-left .logo-wrapper .num {
		position: absolute;
		top: 0;
		right: 0;
		width: 24px;
		height: 16px;
		line-height: 16px;
		text-align: center;
		border-radius: 16px;
		font-size: 9px;
		font-weight: 700;
		color: rgb(255, 255, 255);
		background: rgb(240, 20, 20);
		box-shadow: 0 4px 8px 0 rgba(0,0,0,0.4);
		display: none;
}
.shopcart .content .content-left .price {
		display: inline-block;
		vertical-align: top;
		margin-top: 12px;
		line-height: 24px;
		padding-right: 12px;
		box-sizing: border-box;
		border-right: 1px solid rgba(255,255,255,0.1);
		font-size: 16px;
		font-weight: 700;
}
.shopcart .content .content-left .desc {
    display: inline-block;
    vertical-align: top;
    margin: 12px 0 0 12px;
    line-height: 24px;
    font-size: 10px;
}
.shopcart .content .content-right {
    -webkit-box-flex: 0;
    -ms-flex: 0 0 105px;
    flex: 0 0 105px;
    width: 105px;
}
.shopcart .content .content-right .pay.not-enough {
    background: rgb(43, 51, 59);
}
.shopcart .content .content-right .pay {
    height: 48px;
    line-height: 48px;
    text-align: center;
    font-size: 12px;
    font-weight: 700;
}



